import React, {useState} from "react";
import { useHistory, Link} from "react-router-dom";


//Images
import logo from "../images/logo2.png";
//import logo2 from "../images/logo.png";

//Styles
import  "./WebMenu.css";

//Bootstrap
import {Navbar,
        Nav,
        Container,
        Row,
        Col,
        NavDropdown,
        FormControl,
        Form,
        Button,
  } from 'react-bootstrap';

const WebMenu = props => {
    const history = useHistory();
    const [expanded, setExpanded] = useState(false);

    const menuItems = [
        { key: 'home', name: 'Inicio', onClick:  () => navigateAndCollapse("/") /* () =>  history.push('/')*/},
        { key: 'we', name: 'Quiénes Somos', onClick: () => navigateAndCollapse('/QuienesSomos/')},
        { key: 'serviciosYproyectos', name: 'Servicios y Proyectos', onClick:() => navigateAndCollapse('/ServiciosYProyectos/')},
        { key: 'news', name: 'Publicaciones', onClick: () => navigateAndCollapse('/Publicaciones/')},
      ]

    const covidButtonInfo = { 
        key: 'dashboard',
        name: 'Tablero Covid-19',
        onClick: () => navigateAndCollapse('/TableroCovid/')
    }

    const tableroDeVacunacion = "https://insights.arcgis.com/#/view/456c2dffef0d4dd6973d34d877a5be4c";
    const mortalidadButtonInfo = { 
        key: 'mortalidad',
        name: 'Tablero Vacunación',
        onClick: () => navigateAndCollapse('/EM/'),
    }

    const logoClick = () => {
        setExpanded(false)
        history.push('/')
      } 

    const navigateAndCollapse = (url) => {
        setExpanded(false)
        history.push(url)
    }

    return(
        <React.Fragment>
            <Navbar expand="lg" 
                    expanded={expanded}
            >
            <Navbar.Brand><img className="clickable" onClick={()=>logoClick()} src={logo} alt="Logo"/></Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={() => setExpanded(expanded ? false : "expanded")} />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="ml-auto">
                {menuItems.map((i) => (
                                        <Nav.Link
                                        key={i.key}
                                        className={"navFont ml-4 mr-4"}
                                        onClick={i.onClick}>
                                            {i.name}
                                        </Nav.Link>
                                    ))}
                                        <Nav.Link
                                        key={covidButtonInfo.key}
                                        className="EMButton ml-4 mr-4"
                                        onClick={covidButtonInfo.onClick}>
                                            {covidButtonInfo.name}
                                        </Nav.Link> 

                                        <a href={tableroDeVacunacion} target="_blank" className="" style={{border: "black"}}> 
                                        <Button
                                        key={mortalidadButtonInfo.key}
                                        className="EMButton ml-4 mr-4"
                                        >
                                                
                                                {mortalidadButtonInfo.name} 
                                        </Button>
                                        </a>
                                        
                </Nav>
            </Navbar.Collapse>
            </Navbar>
        </React.Fragment>
    )
}
export default WebMenu;

