import React, { useState } from "react";
import {apiKey} from "../email";
import emailjs from 'emailjs-com';
import { useHistory } from "react-router-dom";

// Resources
import imageFill from "../images/token.png";

//Bootstrap
import {Container,
    Row,
    Col,
    Card,
    Button,
    Form
} from 'react-bootstrap';






const weAreItems = [
    { imageSrc: imageFill,
      key: "KARIN",
      name: 'KARIN SLOWING UMAÑA', 
      message: "Medica y cirujana con Maestría en Salud Pública para paises en Desarrollo. Consultora nacional e internacional en temas de gestión pública y políticas de desarrollo, con énfasis en salud."
    },
    { imageSrc: imageFill,
        name: 'OSCAR CHÁVEZ VALDEZ', 
        key: "OSCAR",
        message: "Ingeniero en Robótica, Analista de Negocios y de Datos con Maestría en Administración Pública. Experiencia en proyectos e inestigación en sociedad civil."
    },
    { imageSrc: imageFill,
        name: 'ALBERTO SIBRIÁN VALLE',
        key: "ALBERTO",
        message: "Economista, Analista de Riesgos con experiencia en implementación de estrategias financieras, de comunicación y cumplimiento."
    },
    { imageSrc: imageFill,
        name: 'CARLOS DUARTE CARRANZA',
        key: "CARLOS",
        message: "Ingeniero en SIstemas, Postgrado Internacional en Sistemas de Información Geográfica y Teledetección Espacial. Investigador asociado al instituto de inestigación y Protección en Ambiente y Sociedad."
    },
    { imageSrc: imageFill,
        name: 'ENRIQUE MALDONADO',
        key: "ENRIQUE",
        message: "Economista, especializado en temas de política social y fiscal. Consultor e investigador en temas de inversión pública, políticas y presupuesto público y derechos humanos."
    },
  ]

      // IAm Component
      const IAm = props => {
        return(
            <Container fluid key={props.id}>
                <Row className="justify-content-md-center">
                <Col>
                   <img src={imageFill} />
                </Col>
                </Row>
                <Row className="justify-content-md-center">
                <Col className="textColorHeader">
                    {props.name}
                </Col>
                </Row>
                <Row className="text-left" className="">
                <Col>
                    <b>{props.message}</b>
                </Col>
                </Row>
            </Container>
    )}

    // WeAre Component
    const WeList = () => {
        return(
        <Container fluid className="my-3">
            <p className="sectionHeader">¿QUIÉNES SOMOS?</p>
            <Row>
                {weAreItems.map((i) => (
                    <Col lg="6" sm="12" key={i.key}>
                        <IAm
                            
                            imgSrc={i.imageSrc}
                            name={i.name}
                            message={i.message}
                        />
                    </Col>
                ))}
            </Row>
        </Container>
    )}

const WeAre = () => {
    const history = useHistory();
    emailjs.init("user_LQOuDV2gVTDs9O9NPeDVP");
    
    const handleSubmit = async (e) => {
        const emailContent =  {
            name: inputName,
            from_name: inputEmail,
            message: inputMessage
        }
        try{
        await emailjs.send(
            "service_f43aexh" , apiKey.TEMPLATE_ID,
            emailContent, "user_LQOuDV2gVTDs9O9NPeDVP"
        ).then(res => {
            alert('Mensaje Enviado')
            window.scrollTo(0, 0);
            history.push("/")
        })
            .catch(err => console.error('There has been an error.  Here some thoughts on the error that occured:', err))
        } catch(error) {
            console.log(error)
        }
    };
    
    const [inputEmail, setInputEmail] = useState(null);
    const [inputName, setInputName] = useState(null);
    const [inputMessage, setInputMessage] = useState(null);
    
    const handleEmailChange = (event) => {
        setInputEmail(event.target.value);
       
    }

    const handleNameChange = (event) => {
        setInputName(event.target.value);
       
    }

    const handleMessageChange = (event) => {
        setInputMessage(event.target.value);
       
    }

    return(
        <React.Fragment>
            <header>
                <Container fluid className="">
                <Card border="light border-0">
                    <Card.Body className="p-5 mx-2">
                        <Card.Title className="sectionHeader text-left">Quiénes somos</Card.Title>
                        <Card.Text className="labText mb-5">
                        <p className="secondColorText">
                        Somos un centro de pensamiento independiente que surge a raíz de la pandemia por COVID-19 conformado por profesionales de distintas disciplinas que decidimos unir nuestras capacidades y experiencia para proponer soluciones basadas en evidencia.
                        Realizamos investigación científica para generar conocimiento sobre los grandes problemas de desarrollo que enfrenta la sociedad guatemalteca.
                        Generamos alternativas de cómo enfrentar estos problemas en los diferentes niveles y entidades de gobierno a través de la innovación y evidencia producida científicamente.
                        Hacemos divulgación científica y de datos para mantener informada a la población. 
                        Fomentamos el dialogo entre la sociedad, sector público y privado para generar redes y alianzas en beneficio del país. 
                        Diseñamos, ejecutamos y evaluamos políticas públicas, programas y proyectos de desarrollo que potencien el uso de tecnología, datos cuantitativos y cualitativos.
                        Apoyamos al fortalecimiento, desarrollo institucional, capacidades técnicas, académicas de instituciones y organizaciones.

                        </p>
                        </Card.Text>
                        <Card.Text className="textHeader mt-5">
                        <p className="secondColorText my-2"><b>Misión</b></p>
                        </Card.Text>
                        <Card.Text className="labText">
                        <p className="secondColorText">
                        Generar conocimiento sobre los grandes problemas de desarrollo que enfrente la sociedad guatemalteca a través de la 
                        investigación científica y el uso de técnicas y tecnologías para análisis de datos e información con el propósito de 
                        mantener informada a la ciudadanía, e incidir en tomadores de decisión.
                        </p>
                        </Card.Text>

                        <Card.Text className="textHeader mt-5">
                        <p className="secondColorText"><b>Visión</b></p>
                        </Card.Text>
                        <Card.Text className="labText my-2">
                        <p className="secondColorText">
                        Ser una organización líder y referente para la toma de decisiones basadas en evidencia, contribuyendo a la 
                        construcción de un estado democrático, transparente moderno e incluyente, a través del diálogo y la innovación en el sector público y privado.
                        </p>
                        </Card.Text>

                    </Card.Body>
                </Card>
                </Container>
            </header>

            
            <Container fluid>
                <main className="py-1">
                 
                </main>
            </Container>

            <Container>
                <div className="d-flex flex-column ">
                    <p className="secondColorText text-left"><b>¡Contáctanos! </b></p>
                    <Form onSubmit={handleSubmit} className="mb-5" method="GET">
                        <Form.Group className="text-left" className="text-left" controlId="contactForm.name">
                            <Form.Label>Nombre</Form.Label>
                            <Form.Control type="text" onChange={handleNameChange}  />
                        </Form.Group>
                        <Form.Group className="text-left" controlId="contactForm.email">
                            <Form.Label>Correo Electrónico</Form.Label>
                            <Form.Control type="email" placeholder="TuCorreo@gmail.com" onChange={handleEmailChange}  />
                        </Form.Group>
                        <Form.Group className="text-left" controlId="contactForm.message">
                            <Form.Label>Mensaje</Form.Label>
                            <Form.Control as="textarea" rows={3} onChange={handleMessageChange}  />
                        </Form.Group>
                        <Button style={Styles.sendButton} className="mb-5" variant="primary" type="submit">
                        Enviar
                        </Button>
                    </Form>
                </div>           
            </Container>
            
        </React.Fragment>
    )
}

export default WeAre;

const Styles = {}

Styles.sendButton = {
    marginRight: "-50%",
    width: "50%"
}

