import React, { useState } from "react";
import {apiKey} from "../email";
import emailjs from 'emailjs-com';
import { useHistory } from "react-router-dom";

// Resources
import imageFill from "../images/token.png";

//Bootstrap
import {Container,
    Row,
    Col,
    Card,
    Button,
    Form
} from 'react-bootstrap';


const SAP = () => {
    const history = useHistory();
    emailjs.init("user_LQOuDV2gVTDs9O9NPeDVP");
    
    const handleSubmit = async (e) => {
        const emailContent =  {
            name: inputName,
            from_name: inputEmail,
            message: inputMessage
        }
        try{
        await emailjs.send(
            "service_f43aexh" , apiKey.TEMPLATE_ID,
            emailContent, "user_LQOuDV2gVTDs9O9NPeDVP"
        ).then(res => {
            alert('Mensaje Enviado')
            window.scrollTo(0, 0);
            history.push("/")
        })
            .catch(err => console.error('There has been an error.  Here some thoughts on the error that occured:', err))
        } catch(error) {
            console.log(error)
        }
    };
    
    const [inputEmail, setInputEmail] = useState(null);
    const [inputName, setInputName] = useState(null);
    const [inputMessage, setInputMessage] = useState(null);
    
    const handleEmailChange = (event) => {
        setInputEmail(event.target.value);
       
    }

    const handleNameChange = (event) => {
        setInputName(event.target.value);
       
    }

    const handleMessageChange = (event) => {
        setInputMessage(event.target.value);
       
    }
    return(
        <React.Fragment>
            <header>
                <Container fluid className="">
                <Card border="light border-0">
                    <Card.Body className="p-3">
                        <Card.Title className="sectionHeader text-left">Servicios y proyectos</Card.Title>
                        <Card.Text className="labText mb-2 mt-5 px-3">
                            <p className="secondColorText">
                            <strong>• Investigación científica.</strong>
                            </p>
                        </Card.Text>

                        <Card.Text className="labText mb-2 px-3">
                            <p className="secondColorText">
                            <strong>• Tableros de control y ciencia de datos.
                            </strong>
                            </p>
                        </Card.Text>

                        <Card.Text className="labText mb-2 px-3">
                            <p className="secondColorText">
                            <strong>• Proyectos de desarrollo, innovación, educación, tecnología, análisis de datos cuantitativos y cualitativos, entre otros.
                            </strong>
                            </p>
                        </Card.Text>

                        <Card.Text className="labText px-3 pb-5">
                            <p className="secondColorText">
                            <strong>• Diagnóstico y capacitación sobre de medidas de bioseguridad COVID-19.
                            </strong>
                            </p>
                        </Card.Text>



                        <Card.Text className="labText">
                        <Container>
                            <div className="d-flex flex-column">
                                <p className="secondColorText"><b>Para información sobre cualquier servicio del Laboratorio de Datos GT ¡Contáctanos!</b></p>
                                <Form onSubmit={handleSubmit} className="mb-5" method="GET">
                                    <Form.Group controlId="contactForm.name">
                                        <Form.Label>Nombre</Form.Label>
                                        <Form.Control type="text" onChange={handleNameChange}  />
                                    </Form.Group>
                                    <Form.Group controlId="contactForm.email">
                                        <Form.Label>Correo Electrónico</Form.Label>
                                        <Form.Control type="email" placeholder="TuCorreo@gmail.com" onChange={handleEmailChange}  />
                                    </Form.Group>
                                    <Form.Group controlId="contactForm.message">
                                        <Form.Label>Mensaje</Form.Label>
                                        <Form.Control as="textarea" rows={3} onChange={handleMessageChange}  />
                                    </Form.Group>
                                    <Button style={Styles.sendButton} className="mb-5" variant="primary" type="submit">
                                    Enviar
                                    </Button>
                                </Form>
                            </div>           
                        </Container>
                        
                        </Card.Text>

                    </Card.Body>
                </Card>
                </Container>
            </header>
        </React.Fragment>
    )
}

export default SAP;

const Styles = {}

Styles.sendButton = {
    marginLeft: "50%",
    width: "50%"
}
